	import {uniHttp} from "../../apis/api.js"
export default {
  data() {
    return {
      homePageMap: [],//轮播图
      repeatedLoading:true,//是否加载更多
	  //商品tabnav 分类
      goodsTabsVoList: [],
      //tab选择的下标
	  chooseNavIndex:0,
      chooseNav: '{{}}',
	  //页面是否上下滚动
      verticalScroll: false,
      winWidth: '',
      winHeight: '',
	  pageLoading:false,//是否正在加载页面
      goodsVo1: [],
      goodsVo2: [],
      goodsVo3: [],
	  currentgoodsVo:[],
	  tabsgoodsVoList:[],//用于承载goodsVo1 goodsVo12 goodsVo3
	  isloadedend:false,//各个tabs列表是否加载结束
	};
  },

  components: {},
  props: {},

  //生命周期函数--监听页面加载
  onLoad: function (options) {
	   this.tabsgoodsVoList=[this.goodsVo1,this.goodsVo2,this.goodsVo3];
	  uni.showLoading({
	  	title:"加载中...",
		mask:true
	  });
	 //this.$store.commit('login');
	 Promise.all(
		[
			uniHttp({//轮播图
				"path":"/goods/api/goodsInternal/getHomePageSowingMap/1/10",
				"header":{
					"aid":"102"
				}
			}),
			uniHttp({//tab列表
				"path":"/goods/api/goodsInternal/getTabsListByCategory/1",
				"header":{
					"aid":"103"
				}
			})
		]
		).then(res=>{
		  uni.hideLoading();
		  console.log(res)
		  let swoingMapData=res[0];
		  let tabsListData=res[1];
		  //轮播图
			if (swoingMapData.shopContentVoList.length > 0) {
			  for (var i = 0; i < swoingMapData.shopContentVoList.length; i++) {
				var img = getApp().globalData.imgUrl + "/" + swoingMapData.shopContentVoList[i].contentWxImg;
				swoingMapData.shopContentVoList[i].contentWxImg = img;
			  }
			  this.homePageMap.push(...swoingMapData.shopContentVoList)
			}
			this.goodsTabsVoList=tabsListData.goodsTabsVoList;
			if(this.goodsTabsVoList.length!=0){
				//获取tabs商品列表
				this.getgoodsVo(0)
			}
		})
		uni.getSystemInfo({
		  success:(res)=>{
			  this.winWidth=res.windowWidth,
			  this.winHeight=res.windowHeight
		  }
		});
  },

  //生命周期函数--监听页面初次渲染完成
  onReady: function () {},

  //生命周期函数--监听页面显示
  onShow: function () {
    this.pageLoading = false;
  },

  // 生命周期函数--监听页面隐藏
  onHide: function () {},

  //生命周期函数--监听页面卸载
  onUnload: function () {},

  //页面相关事件处理函数--监听用户下拉动作
  onPullDownRefresh: function () {},

  //页面上拉触底事件的处理函数
  onReachBottom: function () {
    //今日主推=0  新品=1   ding=2
	if (this.repeatedLoading) {
		console.log('onReachBottom')
		this.repeatedLoading=false;
		this.goodsTabsVoList[this.chooseNavIndex].page = this.goodsTabsVoList[this.chooseNavIndex].page + 1;
		this.getgoodsVo(this.chooseNavIndex);
	}
	console.log(this.repeatedLoading)
  },

  // 用户点击右上角分享

  onShareAppMessage: function () {},
  
  onPageScroll: function (e) {
    uni.createSelectorQuery().select('.jingxuan-main').boundingClientRect(rect=>{
      if (rect.top <= 34) {
          this.verticalScroll=true
      } else {
          this.verticalScroll=false
      }
    }).exec();
  },
  methods: {
   //点击banner进去商品详情页
    goBanner: function (item) {
      if (item.contentType == 2) {
        uni.navigateTo({
          url: '/pages/h5/h5?h5Url=' + item.h5Url
        });
      } else if (item.contentType == 1) {
        uni.navigateTo({
          url: '/pages/commodity_details/commodity_details?goodsId=' + item.desc
        });
      }
    },
	//点击商品进去商品详情页
    commodity_details: function (goodsid) {
      if (!this.pageLoading) {
        this.pageLoading= true;
		uni.navigateTo({
		  url: '/pages/commodity_details/commodity_details?goodsId=' + goodsid
		});
        
      }
    },
	//tab切换
    chooseNavFun: function (index) {
		this.chooseNavIndex=index;
		this.currentgoodsVo=this.tabsgoodsVoList[index];
		this.tabsgoodsVoList=[this.goodsVo1,this.goodsVo2,this.goodsVo3];
		if(!this.tabsgoodsVoList[index].length){
			this.isloadedend=false;
			this.getgoodsVo(index);
		}
    },
	//获取tabs分类对应下的商品列表
    getgoodsVo(index){
		if(!this.isloadedend){
			this.repeatedLoading=false;
			uni.showLoading({
				title:"加载中...",
				mask:true
			});
			let page=this.goodsTabsVoList[index].page.toString();
			let id=this.goodsTabsVoList[index].id.toString();	
			uniHttp({
				"path":`/goods/api/goodsInternal/getGoodsListByHome/${page}/${id}`,
				"header":{
					"aid":"104"
				}
			}).then(res=>{
				this.repeatedLoading= true;
				let goodslist=res;
				if (goodslist.goodsVoList.length > 0) {
					for (var i = 0; i < goodslist.goodsVoList.length; i++) {
					  var img = getApp().globalData.imgUrl + "/" + goodslist.goodsVoList[i].goodsPic;
					  goodslist.goodsVoList[i].goodsPic = img;
					}
					this.tabsgoodsVoList[index].push(...goodslist.goodsVoList);
					this.currentgoodsVo=this.tabsgoodsVoList[index];
				}else{
					this.isloadedend=true;
					console.log('isend');
				}
				//隐藏导航条加载动画。

				uni.hideNavigationBarLoading();

				uni.stopPullDownRefresh();
				uni.hideLoading();
			})
		}else{
			//this.repeatedLoading=true;
			console.log('我是有底线的')
		}
    }

  }
};
